import { useState, useEffect } from 'react';
import { api } from '../../services/api';
import styles from './products.module.css';

interface Product {
  _id: string;
  vendor: string | { _id: string; storeName: string };
  name: string;
  description?: string;
  price: number;
  category?: string;
  image?: string;
  isAvailable: boolean;
  stock: number;
  createdAt?: string;
}

interface Vendor {
  _id: string;
  storeName: string;
}

export default function AdminProducts() {
  const [products, setProducts] = useState<Product[]>([]);
  const [vendors, setVendors] = useState<Vendor[]>([]);
  const [loading, setLoading] = useState(false);
  const [selectedVendor, setSelectedVendor] = useState<string>('all');

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      setLoading(true);
      const [productsData, vendorsData] = await Promise.all([
        api.products.getAll(),
        api.vendors.getAll(),
      ]);
      setProducts(productsData);
      setVendors(vendorsData);
    } catch (error) {
      console.error('Error loading data:', error);
      alert('Failed to load products');
    } finally {
      setLoading(false);
    }
  };

  const getVendorName = (vendorId: string | { _id: string; storeName: string }): string => {
    if (typeof vendorId === 'object' && vendorId.storeName) {
      return vendorId.storeName;
    }
    const vendor = vendors.find((v) => v._id === vendorId);
    return vendor?.storeName || 'Unknown Vendor';
  };

  const getVendorId = (vendor: string | { _id: string; storeName: string }): string => {
    if (typeof vendor === 'object') {
      return vendor._id;
    }
    return vendor;
  };

  const filteredProducts =
    selectedVendor === 'all'
      ? products
      : products.filter((p) => getVendorId(p.vendor) === selectedVendor);

  return (
    <div className={styles.productsContainer}>
      <div className={styles.productsHeader}>
        <h1 className={styles.productsTitle}>All Products</h1>
      </div>

      {loading ? (
        <div className={styles.productsLoading}>Loading products...</div>
      ) : (
        <>
          <div className={styles.productsStats}>
            <div className={styles.statCard}>
              <div className={styles.statLabel}>Total Products</div>
              <div className={styles.statValue}>{filteredProducts.length}</div>
            </div>
            <div className={styles.statCard}>
              <div className={styles.statLabel}>Available</div>
              <div className={styles.statValue}>
                {filteredProducts.filter((p) => p.isAvailable).length}
              </div>
            </div>
            <div className={styles.statCard}>
              <div className={styles.statLabel}>Out of Stock</div>
              <div className={styles.statValue}>{filteredProducts.filter((p) => p.stock === 0).length}</div>
            </div>
            <div className={styles.statCard}>
              <div className={styles.statLabel}>Low Stock</div>
              <div className={styles.statValue}>
                {filteredProducts.filter((p) => p.stock > 0 && p.stock < 10).length}
              </div>
            </div>
          </div>

          <section className={styles.productsSection}>
            <div className={styles.sectionHeader}>
              <h2 className={styles.sectionTitle}>Product Inventory</h2>
              <div className={styles.filterControls}>
                <label className={styles.filterLabel}>Filter by Vendor:</label>
                <select
                  className={styles.filterSelect}
                  value={selectedVendor}
                  onChange={(e) => setSelectedVendor(e.target.value)}
                >
                  <option value="all">All Vendors</option>
                  {vendors.map((vendor) => (
                    <option key={vendor._id} value={vendor._id}>
                      {vendor.storeName}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <div className={styles.productsTableWrap}>
              {filteredProducts.length === 0 ? (
                <div className={styles.emptyState}>
                  <p>No products found</p>
                </div>
              ) : (
                <table className={styles.productsTable}>
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Vendor</th>
                      <th>Category</th>
                      <th>Price</th>
                      <th>Stock</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {filteredProducts.map((product) => (
                      <tr key={product._id}>
                        <td>
                          <div className={styles.productNameCell}>
                            {product.image && (
                              <img src={product.image} alt={product.name} className={styles.productThumb} />
                            )}
                            <div>
                              <div className={styles.productName}>{product.name}</div>
                              {product.description && (
                                <div className={styles.productDesc}>{product.description.substring(0, 50)}...</div>
                              )}
                            </div>
                          </div>
                        </td>
                        <td>{getVendorName(product.vendor)}</td>
                        <td>{product.category || '—'}</td>
                        <td className={styles.priceCell}>₹{product.price.toFixed(2)}</td>
                        <td className={styles.stockCell}>
                          <span
                            className={`${styles.stockBadge} ${
                              product.stock === 0 ? styles.stockBadgeOutOfStock : product.stock < 10 ? styles.stockBadgeLowStock : ''
                            }`}
                          >
                            {product.stock}
                          </span>
                        </td>
                        <td>
                          <span className={`${styles.statusBadge} ${product.isAvailable ? styles.statusBadgeAvailable : styles.statusBadgeUnavailable}`}>
                            {product.isAvailable ? 'Available' : 'Disabled'}
                          </span>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}
            </div>
          </section>
        </>
      )}
    </div>
  );
}
